import { AnyField } from "utils/types";

const getGap = (count: number) => {
  return new Array(count).fill(0).reduce((a, b) => a + " ", "");
};

export const getType = (type: AnyField, ident: number, identStep: number = 3): string => {
  const docType = (fields: AnyField[], multiple: boolean = false) => {
    return fields.length
      ? `{\n${fields.reduce(
          (a, b) =>
            a +
            `${getGap(ident)}${
              b.id.includes(" ") || b.id.includes("-") ? `"${b.id}"` : b.id
            }: ${getType(b, ident + identStep)};\n`,
          ""
        )}${getGap(ident - identStep)}${multiple ? "}[]" : "}"}`
      : multiple
      ? "any[]"
      : "null";
  };

  const types = {
    password: () => "string",
    string: () => "string",
    "rich-text": () => "string",
    json: () => "object",
    map: () => "object",
    array: () => "any[]",
    media: (x: AnyField) => (x.type === "media" && x.mediaSingle ? "string" : "string[]"),
    date: () => "string",
    enum: () => "string[]",
    number: () => "number",
    boolean: () => "boolean",
    relation: (x: AnyField) =>
      x.type === "relation" && x.relationOneToOne ? "string" : "string[]",
    collection: (x: AnyField) => docType(x.type === "collection" ? x.collectionFields : [], true),
    document: (x: AnyField) => docType(x.type === "document" ? x.documentFields : []),
    script: () => "object",
    "script-V2": () => "object",
    any: () => "any",
    "hashed-password": () => "string",
    color: () => "string",
  };
  return `${types[type.type](type)}`;
};
